import React from 'react';
import {useState,useEffect,lazy,Suspense} from 'react';
import '../../css/section.grid.project.css';
//import SectionCardproject from './section.card.project';
import Loader from '../../components2/section/LoaderInterno';
const Tarjetas = lazy(()=>import('./section.card.project'))

const SectionGridProject = ()=>{
  const [cards,setCards] = useState([]);
  useEffect(()=>{

    const handlecards = async()=>{
      try{
        const res = await fetch("https://portafolioapi.herokuapp.com/cards/");
        const obj = await res.json();
        setCards(obj._embedded.cardsList);
      }catch(e){

      }

    }
    handlecards()
  },[])                     
    return (
<div className="columns is-multiline is-mobile is-primary">
  {cards.map((el)=>(
    <div className="column is-one-quarter" id="is_one_quarter">
      <Suspense fallback={<Loader/>}>
        <Tarjetas data={el}/>
      </Suspense>
    </div>
  ))}

</div>
    )
}
export default SectionGridProject
